import React, { useState } from "react";
import { Col, Input, Row, Space, Typography } from "antd";
import { InstagramOutlined, FacebookOutlined } from "@ant-design/icons";
import "./Header.scss";
import "../../styles/search.scss";
import { Link, useHistory } from "react-router-dom";
import { ISearchData } from "../../types";
import { getSearchData } from "../../helpers";

interface IHeaderProps {
  onResult: (result: ISearchData | null) => void;
}

const Header: React.FC<IHeaderProps> = ({ onResult }) => {
  const [searchValue, setSearchValue] = useState("");
  const history = useHistory();

  const search = async () => {
    if (searchValue !== "") {
      history.push("/articles");
      onResult(null);

      const searchData = await getSearchData(searchValue);
      onResult(searchData);
    }
  };

  return (
    <Row align="middle" className="header">
      <Col span={7}>
        <Row justify="center">
          <Link to="/">
            <div className="logo" />
          </Link>
        </Row>
      </Col>
      <Col span={13}>
        <Input.Search
          value={searchValue}
          onChange={(e) => setSearchValue(e.target.value)}
          placeholder="Введіть інформацію для пошуку"
          size="large"
          style={{ marginTop: 40, borderRadius: 30 }}
          className="search"
          onSearch={search}
        />

        <Space size={20} style={{ marginTop: 15 }}>
          <Link to="/">
            <Typography.Text
              style={{ color: "white", fontSize: 16, fontWeight: "bold" }}
            >
               {"Головна".toUpperCase()}
            </Typography.Text>
          </Link>
          <Link to="/about">
            <Typography.Text
              style={{ color: "white", fontSize: 16, fontWeight: "bold" }}
            >
               {"Про нас".toUpperCase()}
            </Typography.Text>
          </Link>
          <Typography.Link
            href="https://www.youtube.com/watch?v=k822LE5T22k"
            target="blank"
            style={{ color: "white", fontSize: 16, fontWeight: "bold" }}
          >
             {"Пояснення щодо роботи з сайтом".toUpperCase()}
          </Typography.Link>
        </Space>
      </Col>
      <Col span={4}>
        <Row justify="center">
          <Typography.Link href="https://www.instagram.com/itismediacheck/" target="blank">
            <InstagramOutlined className="social" />
          </Typography.Link>
          <Typography.Link href="" target="blank">
            <FacebookOutlined className="social" />
          </Typography.Link>
        </Row>
      </Col>
    </Row>
  );
};

export default Header;
