import React from "react";
import { Button, Card, Col, Row, Tag, Typography } from "antd";
import { ISource } from "../types";
import { Link } from "react-router-dom";

const BestSources: React.FC = () => {
  let sources: ISource[] = require("../sources.json");
  sources = sources.slice(0, 10);

  return (
    <Col span={5}>
      <Typography.Title level={5}>
        {"Найдостовірніші ресурси".toUpperCase()}
      </Typography.Title>
      <Card style={{ borderRadius: 10 }}>
        {sources.map((source: ISource, index: number) => (
          <Row
            key={index}
            align="middle"
            justify="space-between"
            style={{ marginBottom: 10 }}
          >
            <Link to={`/sources/${source.site}`}>
              <Typography.Text>{source.site}</Typography.Text>
            </Link>
            <Tag color="red">{source.rate}</Tag>
          </Row>
        ))}

        <Link to="/sources">
          <Button style={{ width: "100%", marginTop: 10 }}>
            {"Всі ресурси"}
          </Button>
        </Link>
      </Card>
    </Col>
  );
};

export default BestSources;
