import React, { useEffect, useState } from "react";
import { useParams } from "react-router";
import { ISource } from "../types";
import { Card, Col, Collapse, Rate, Row, Typography } from "antd";
const { Panel } = Collapse;

interface IParams {
  site: string;
}

const Source: React.FC = () => {
  const sources: ISource[] = require("../sources.json");

  const { site } = useParams<IParams>();
  const [source, setSource] = useState<ISource>();

  useEffect(() => {
    for (const source of sources) {
      if (source.site === site) {
        setSource(source);
      }
    }
  }, [sources, site]);

  return (
    <Col span={17}>
      <Card style={{ borderRadius: 10 }}>
        <Row justify="space-between">
          <Typography.Link
            href={`https://${source?.site}`}
            target="blank"
            style={{ marginBottom: 20, fontSize: 40, color: "#333" }}
          >
            {source?.site}
          </Typography.Link>
          <Rate allowHalf disabled value={source?.rate} />
        </Row>
        <Typography.Paragraph>{source?.description}</Typography.Paragraph>

        {source && source.descriptionOptions?.length > 0 && (
          <Collapse defaultActiveKey={["0"]}>
            {source.descriptionOptions.map((option: string, index: number) => (
              <Panel header={`Ознака ${index + 1}`} key={index}>
                <p>{option}</p>
              </Panel>
            ))}
          </Collapse>
        )}
      </Card>
    </Col>
  );
};

export default Source;
