import React, { useState } from "react";
import { Route } from "react-router";
import { Switch } from "react-router-dom";
import { Row } from "antd";
import BestSources from "./components/BestSources";
import Header from "./components/Header/Header";
import VideoContent from "./components/VideoContent";
import SourceSearchAndInfo from "./components/SourceSearchAndInfo";
import Sources from "./components/Sources";
import Source from "./components/Source";
import About from "./components/About";
import Articles from "./components/Articles";
import { ISearchData } from "./types";

const App: React.FunctionComponent = () => {
  const [searchData, setSearchData] = useState<ISearchData | null>(null);

  return (
    <>
      <Header onResult={setSearchData} />

      <Row gutter={40} style={{ margin: 20 }}>
        <Switch>
          <Route exact path="/">
            <BestSources />
            <VideoContent />
          </Route>
          <Route exact path="/sources" component={Sources} />
          <Route exact path="/sources/:site" component={Source} />
          <Route exact path="/articles">
            <Articles searchDataProp={searchData} />
          </Route>
          <Route exact path="/about" component={About} />
        </Switch>
        <SourceSearchAndInfo />
      </Row>
    </>
  );
};

export default App;
