import { Card, Col, Rate, Row, Typography } from "antd";
import React from "react";
import { Link } from "react-router-dom";
import { ISource } from "../types";

const Sources: React.FC = () => {
  const sources: ISource[] = require("../sources.json");

  return (
    <Col span={17}>
      <Row gutter={[40, 40]}>
        {sources.map((source: ISource, index: number) => (
          <Col span={12} key={index}>
            <Link to={`/sources/${source.site}`}>
              <Card style={{ borderRadius: 10 }}>
                <Row justify="space-between" style={{ marginBottom: 10 }}>
                  <Typography.Title level={5}>{source.site}</Typography.Title>
                  <Rate allowHalf disabled value={source.rate} />
                </Row>
                <Typography.Text>
                  {source.description.split("У ході аналізу даного")[0]}
                </Typography.Text>
              </Card>
            </Link>
          </Col>
        ))}
      </Row>
    </Col>
  );
};

export default Sources;
