import axios from "axios";
import { ISearchData, ISource } from "./types";

export const getSearchData = async (searchValue: string, startIndex?: number): Promise<ISearchData> => {
  const startPart = startIndex ? `&start=${startIndex}` : "";
  const res = await axios.get(
    `https://www.googleapis.com/customsearch/v1?key=AIzaSyAvT334wFD-pdqk6GDWsn6xmKV5_IXYEPQ&cx=de705c6a934a1748b&q=${searchValue}${startPart}`
  );

  return res.data;
};

export const getSourceBySite = (site: string): ISource | null => {
  const sources: ISource[] = require("./sources.json");

  for (const source of sources) {
    if (site.includes(source.site)) {
      return source;
    }
  }

  return null;
};
