import React from "react";
import { useHistory } from "react-router-dom";
import { Card, Col, Row, Select, Typography } from "antd";
import "../styles/search.scss";
import { ISource } from "../types";

const { Option } = Select;

const SourceSearchAndInfo: React.FC = () => {
  const sources: ISource[] = require('../sources.json');
  const history = useHistory();

  const onSearchChange = (val: string) => {
    history.push(`/sources/${val}`);
  };

  return (
    <Col span={7}>
      <Typography.Title level={5}>
        {"Для пошуку конкретного інформаційного ресурсу ви можете скористатися пошуковим полем внизу".toUpperCase()}
      </Typography.Title>

      <Select
        showSearch
        style={{ width: "100%", marginBottom: 40 }}
        placeholder="Введіть необхідну назву"
        onChange={onSearchChange}
      >
        {sources.map((source: ISource, index: number) => (
          <Option key={index} value={source.site}>{source.site}</Option>
        ))}
      </Select>

      <Typography.Title level={5}>
        {"Пояснення до присвоєння рейтингу достовірності".toUpperCase()}
      </Typography.Title>
      <Card style={{ borderRadius: 10 }}>
        <Row style={{ marginBottom: 10 }}>
          <Typography.Text>
            Задля того, щоб будь хто міг дізнатися чи вартий інформаційний
            ресурс довіри, ми створили систему оцінювання - «рейтинг
            достовірості». Оцінювання кожного з ресурсів, що наявні в нашому
            рейтингу, підлягає присвоєння оціночного номеру зі шкалою в 5 балів,
            де 5 отримує найнадійніший, а 0 - ненадійний відповідно. У разі
            виникненя запитань, щодо причини присвоєня того чи іншого балу, ви
            можете перейти до вкладки з детальним рейтингом де описуються
            причини чому так відбулося.
          </Typography.Text>
        </Row>
        <Row style={{ textAlign: "center", display: "block" }}>
          <Typography.Text type="secondary">
            Дякую за увагу і нехай щастить!
          </Typography.Text>
        </Row>
        <Row style={{ textAlign: "center", display: "block" }}>
          <Typography.Text type="secondary">
            Команда проекту "FactSheet"
          </Typography.Text>
        </Row>
      </Card>
    </Col>
  );
};

export default SourceSearchAndInfo;
