import React, { useEffect, useState } from "react";
import { Button, Card, Col, Rate, Row, Spin, Typography } from "antd";
import { LoadingOutlined } from "@ant-design/icons";
import { ISearchData, ISearchItem } from "../types";
import { getSearchData, getSourceBySite } from "../helpers";

interface IArticleProps {
  searchDataProp: ISearchData | null;
}

const Articles: React.FC<IArticleProps> = ({ searchDataProp }) => {
  const [articles, setArticles] = useState<ISearchItem[]>([]);
  const [searchData, setSearchData] = useState<ISearchData | null>(null);

  useEffect(() => {
    setSearchData(searchDataProp);
    setArticles([]);
  }, [searchDataProp]);

  useEffect(() => {
    if (searchData?.items) {
      setArticles((prev: ISearchItem[]) => {
        return [...prev, ...searchData.items];
      });
    }
  }, [searchData]);

  const loadMore = async () => {
    const nextPageInfo = searchData?.queries.nextPage;

    if (nextPageInfo) {
      const searchData = await getSearchData(
        nextPageInfo[0].searchTerms,
        nextPageInfo[0].startIndex
      );

      setSearchData(searchData);
    }
  };

  return searchData ? (
    <Col span={17}>
      <Row gutter={[40, 40]}>
        {articles.map((article: ISearchItem, index: number) => (
          <Col span={12} key={index}>
            <a href={article.link} target="blank">
              <Card key={index} style={{ borderRadius: 10 }}>
                <Row justify="space-between" style={{ marginBottom: 10 }}>
                  <Typography.Title level={5}>
                    {getSourceBySite(article.link)?.site}
                  </Typography.Title>
                  <Rate
                    allowHalf
                    disabled
                    value={getSourceBySite(article.link)?.rate}
                  />
                </Row>
                <Typography.Text>{article.title}</Typography.Text>
              </Card>
            </a>
          </Col>
        ))}
      </Row>
      <Row>
        <Button
          onClick={loadMore}
          style={{ width: "100%", margin: "40px 0", borderRadius: 10 }}
        >
          Завантажити ще
        </Button>
      </Row>
    </Col>
  ) : (
    <Col span={17}>
      <Row justify="center">
        <Spin
          style={{ marginTop: 250 }}
          indicator={
            <LoadingOutlined style={{ fontSize: 100, color: "#692e90" }} spin />
          }
        />
      </Row>
    </Col>
  );
};

export default Articles;
